import { supabase } from './supabase';
import { MenuDB, CreateMenuData, UpdateMenuData } from '../types';

//    
export const getMenus = async (storeId: string): Promise<MenuDB[]> => {
  if (!storeId) {
    throw new Error(' ID .');
  }

  const { data, error } = await supabase
    .from('menus')
    .select('*')
    .eq('store_id', storeId)
    .order('category', { ascending: true });

  if (error) {
    console.error('   :', error);
    throw new Error(`   : ${error.message}`);
  }

  return data || [];
};

//    
export const getMenu = async (menuId: string): Promise<MenuDB> => {
  if (!menuId) {
    throw new Error(' ID .');
  }

  const { data, error } = await supabase
    .from('menus')
    .select('*')
    .eq('id', menuId)
    .single();

  if (error) {
    console.error('   :', error);
    throw new Error(`   : ${error.message}`);
  }

  return data;
};

//  
export const createMenu = async (menuData: CreateMenuData): Promise<MenuDB> => {
  //   
  if (!menuData.store_id) {
    throw new Error(' ID .');
  }
  if (!menuData.name?.trim()) {
    throw new Error(' .');
  }
  if (!menuData.price || menuData.price <= 0) {
    throw new Error('  .');
  }

  console.log('    :', menuData);
  
  const insertData = {
    ...menuData,
    category: menuData.category || ''
  };
  
  console.log('   :', insertData);
  
  const { data, error } = await supabase
    .from('menus')
    .insert(insertData)
    .select()
    .single();

  if (error) {
    console.error('   :', error);
    throw new Error(`  : ${error.message}`);
  }

  console.log('    :', data);
  return data;
};

//  
export const updateMenu = async (menuId: string, updateData: UpdateMenuData): Promise<MenuDB> => {
  if (!menuId) {
    throw new Error(' ID .');
  }
  if (updateData.name !== undefined && !updateData.name?.trim()) {
    throw new Error(' .');
  }
  if (updateData.price !== undefined && updateData.price <= 0) {
    throw new Error('  .');
  }

  console.log('   :', { menuId, updateData });
  
  const { data, error } = await supabase
    .from('menus')
    .update(updateData)
    .eq('id', menuId)
    .select()
    .single();

  if (error) {
    console.error('   :', error);
    throw new Error(`  : ${error.message}`);
  }

  console.log('   :', data);
  return data;
};

//  
export const deleteMenu = async (menuId: string): Promise<void> => {
  if (!menuId) {
    throw new Error(' ID .');
  }

  const { error } = await supabase
    .from('menus')
    .delete()
    .eq('id', menuId);

  if (error) {
    console.error('  :', error);
    throw new Error(`  : ${error.message}`);
  }
};

//   
export const toggleMenuAvailability = async (menuId: string, isAvailable: boolean): Promise<MenuDB> => {
  if (!menuId) {
    throw new Error(' ID .');
  }

  const { data, error } = await supabase
    .from('menus')
    .update({ is_available: isAvailable })
    .eq('id', menuId)
    .select()
    .single();

  if (error) {
    console.error('   :', error);
    throw new Error(`   : ${error.message}`);
  }

  return data;
};
