import { supabase } from './supabase';

export interface Inquiry {
  id: string;
  name: string;
  phone: string;
  store_name: string;
  status: '' | '';
  created_at: string;
  updated_at: string;
}

//   
export const getInquiries = async (): Promise<Inquiry[]> => {
  try {
    const { data, error } = await supabase
      .from('inquiries')
      .select('*')
      .order('created_at', { ascending: false });

    if (error) {
      console.error('  :', error);
      throw error;
    }

    return data || [];
  } catch (error) {
    console.error('  :', error);
    throw error;
  }
};

//  
export const createInquiry = async (inquiryData: {
  name: string;
  phone: string;
  store_name: string;
}): Promise<Inquiry> => {
  try {
    // RLS   service role key   SQL 
    const { data, error } = await supabase
      .from('inquiries')
      .insert({
        name: inquiryData.name,
        phone: inquiryData.phone,
        store_name: inquiryData.store_name,
        status: '',
        created_at: new Date().toISOString(),
        updated_at: new Date().toISOString()
      })
      .select()
      .single();

    if (error) {
      console.error('  :', error);
      // RLS     
      if (error.code === '42501') {
        console.log('RLS   - Supabase  SQL :');
        console.log('DROP POLICY IF EXISTS "Anyone can create inquiries" ON public.inquiries;');
        console.log('CREATE POLICY "Allow anonymous inquiry creation" ON public.inquiries FOR INSERT WITH CHECK (true);');
        throw new Error('   .  .');
      }
      throw error;
    }

    return data;
  } catch (error) {
    console.error('  :', error);
    throw error;
  }
};

//   
export const updateInquiryStatus = async (inquiryId: string, status: '' | ''): Promise<void> => {
  try {
    const { error } = await supabase
      .from('inquiries')
      .update({
        status,
        updated_at: new Date().toISOString()
      })
      .eq('id', inquiryId);

    if (error) {
      console.error('   :', error);
      throw error;
    }
  } catch (error) {
    console.error('   :', error);
    throw error;
  }
};

//  
export const deleteInquiry = async (inquiryId: string): Promise<void> => {
  try {
    const { error } = await supabase
      .from('inquiries')
      .delete()
      .eq('id', inquiryId);

    if (error) {
      console.error('  :', error);
      throw error;
    }
  } catch (error) {
    console.error('  :', error);
    throw error;
  }
};
