import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useNewAuth } from '../../hooks/useNewAuth';
import { registerOwner } from '../../lib/authApi';
import Header from '../../components/Header';
import Footer from '../../components/Footer';

export default function Login() {
  const navigate = useNavigate();
  const { login } = useNewAuth();
  const [isLogin, setIsLogin] = useState(true);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  //   
  const [loginForm, setLoginForm] = useState({
    password: ''
  });

  //   
  const [registerForm, setRegisterForm] = useState({
    name: '',
    phone: '',
    password: '',
    confirmPassword: ''
  });

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    try {
      //      (  )
      await login('', loginForm.password);
      navigate('/admin-dashboard');
    } catch (error: any) {
      setError(error.message || ' .');
    } finally {
      setLoading(false);
    }
  };

  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    setSuccess('');

    //  
    if (registerForm.password !== registerForm.confirmPassword) {
      setError('  .');
      setLoading(false);
      return;
    }

    //    ( 4)
    if (registerForm.password.length !== 4) {
      setError(' 4  .');
      setLoading(false);
      return;
    }

    //   
    if (!/^\d{4}$/.test(registerForm.password)) {
      setError(' 4  .');
      setLoading(false);
      return;
    }

    try {
      await registerOwner({
        name: registerForm.name,
        phone: registerForm.phone,
        password: registerForm.password
      });
      
      setSuccess(' .      .');
      setRegisterForm({
        name: '',
        phone: '',
        password: '',
        confirmPassword: ''
      });
      
      // 3    
      setTimeout(() => {
        setIsLogin(true);
        setSuccess('');
      }, 3000);
    } catch (error: any) {
      setError(error.message || ' .');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-orange-50 to-white">
      <Header />
      
      <div className="flex items-center justify-center min-h-[calc(100vh-200px)] px-4">
        <div className="w-full max-w-md">
          <div className="bg-white rounded-2xl shadow-xl p-8 border border-gray-100">
            {/*  */}
            <div className="text-center mb-8">
              <div className="w-16 h-16 bg-gradient-to-br from-orange-500 to-orange-600 rounded-2xl flex items-center justify-center mx-auto mb-4 shadow-lg">
                <span className="text-white font-bold text-2xl">P</span>
              </div>
              <h1 className="text-2xl font-bold text-gray-800 mb-2">PayTalk</h1>
              <p className="text-gray-500">
                {isLogin ? ' ' : ' '}
              </p>
            </div>

            {/* /  */}
            {error && (
              <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-lg mb-6">
                {error}
              </div>
            )}
            
            {success && (
              <div className="bg-green-50 border border-green-200 text-green-700 px-4 py-3 rounded-lg mb-6">
                {success}
              </div>
            )}

            {/*   */}
            {isLogin ? (
              <form onSubmit={handleLogin} className="space-y-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    
                  </label>
                  <input
                    type="password"
                    value={loginForm.password}
                    onChange={(e) => setLoginForm({...loginForm, password: e.target.value})}
                    className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                    placeholder="4   "
                    maxLength={4}
                    required
                  />
                </div>

                <button
                  type="submit"
                  disabled={loading}
                  className="w-full bg-gradient-to-r from-orange-500 to-orange-600 hover:from-orange-600 hover:to-orange-700 text-white py-3 px-4 rounded-xl font-semibold transition-all duration-200 shadow-lg hover:shadow-xl disabled:opacity-50"
                >
                  {loading ? ' ...' : ''}
                </button>
              </form>
            ) : (
              /*   */
              <form onSubmit={handleRegister} className="space-y-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    
                  </label>
                  <input
                    type="text"
                    value={registerForm.name}
                    onChange={(e) => setRegisterForm({...registerForm, name: e.target.value})}
                    className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                    placeholder=" "
                    required
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    
                  </label>
                  <input
                    type="tel"
                    value={registerForm.phone}
                    onChange={(e) => {
                      //   
                      const value = e.target.value.replace(/[^0-9]/g, '');
                      setRegisterForm({...registerForm, phone: value});
                    }}
                    className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                    placeholder="01012345678"
                    maxLength={11}
                    required
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     (4 )
                  </label>
                  <input
                    type="password"
                    value={registerForm.password}
                    onChange={(e) => {
                      //   
                      const value = e.target.value.replace(/[^0-9]/g, '');
                      setRegisterForm({...registerForm, password: value});
                    }}
                    className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                    placeholder="4   "
                    maxLength={4}
                    required
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     
                  </label>
                  <input
                    type="password"
                    value={registerForm.confirmPassword}
                    onChange={(e) => {
                      //   
                      const value = e.target.value.replace(/[^0-9]/g, '');
                      setRegisterForm({...registerForm, confirmPassword: value});
                    }}
                    className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                    placeholder="4    "
                    maxLength={4}
                    required
                  />
                </div>

                <button
                  type="submit"
                  disabled={loading}
                  className="w-full bg-gradient-to-r from-blue-500 to-blue-600 hover:from-blue-600 hover:to-blue-700 text-white py-3 px-4 rounded-xl font-semibold transition-all duration-200 shadow-lg hover:shadow-xl disabled:opacity-50"
                >
                  {loading ? ' ...' : ''}
                </button>
              </form>
            )}

            {/*   */}
            <div className="mt-6 text-center">
              <button
                onClick={() => {
                  setIsLogin(!isLogin);
                  setError('');
                  setSuccess('');
                }}
                className="text-orange-600 hover:text-orange-700 font-medium"
              >
                {isLogin ? '  ? ' : '  ? '}
              </button>
            </div>
          </div>
        </div>
      </div>
      
      <Footer />
    </div>
  );
}
