export interface PushNotificationData {
  title: string;
  body: string;
  icon?: string;
  badge?: string;
  data?: any;
}

//    
export const requestNotificationPermission = async (): Promise<boolean> => {
  if (!('Notification' in window)) {
    console.log('    .');
    return false;
  }

  if (Notification.permission === 'granted') {
    return true;
  }

  if (Notification.permission === 'denied') {
    console.log(' .');
    return false;
  }

  const permission = await Notification.requestPermission();
  return permission === 'granted';
};

// Service Worker 
export const registerServiceWorker = async (): Promise<ServiceWorkerRegistration | null> => {
  if (!('serviceWorker' in navigator)) {
    console.log('  Service Worker  .');
    return null;
  }

  try {
    const registration = await navigator.serviceWorker.register('/sw.js');
    console.log('Service Worker  :', registration);
    return registration;
  } catch (error) {
    console.error('Service Worker  :', error);
    return null;
  }
};

//   
export const subscribeToPush = async (): Promise<PushSubscription | null> => {
  const registration = await navigator.serviceWorker.ready;
  
  if (!registration.pushManager) {
    console.log('    .');
    return null;
  }

  try {
    const subscription = await registration.pushManager.subscribe({
      userVisibleOnly: true,
      applicationServerKey: urlBase64ToUint8Array(process.env.VITE_VAPID_PUBLIC_KEY || '')
    });
    
    console.log('  :', subscription);
    return subscription;
  } catch (error) {
    console.error('  :', error);
    return null;
  }
};

// VAPID   
function urlBase64ToUint8Array(base64String: string): Uint8Array {
  const padding = '='.repeat((4 - base64String.length % 4) % 4);
  const base64 = (base64String + padding)
    .replace(/-/g, '+')
    .replace(/_/g, '/');

  const rawData = window.atob(base64);
  const outputArray = new Uint8Array(rawData.length);

  for (let i = 0; i < rawData.length; ++i) {
    outputArray[i] = rawData.charCodeAt(i);
  }
  return outputArray;
}

//     
export const savePushSubscription = async (subscription: PushSubscription, userId: string): Promise<boolean> => {
  try {
    const response = await fetch('/api/push/subscribe', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        userId,
        subscription: subscription.toJSON()
      })
    });

    if (response.ok) {
      console.log('    ');
      return true;
    } else {
      console.error('    ');
      return false;
    }
  } catch (error) {
    console.error('    :', error);
    return false;
  }
};

//    (   )
export const initializePushNotifications = async (userId: string): Promise<boolean> => {
  try {
    // 1.   
    const hasPermission = await requestNotificationPermission();
    if (!hasPermission) {
      console.log('  .');
      return false;
    }

    // 2. Service Worker 
    const registration = await registerServiceWorker();
    if (!registration) {
      console.log('Service Worker  ');
      return false;
    }

    // 3.  
    const subscription = await subscribeToPush();
    if (!subscription) {
      console.log('  ');
      return false;
    }

    // 4.    
    const saved = await savePushSubscription(subscription, userId);
    if (!saved) {
      console.log('    ');
      return false;
    }

    console.log('   ');
    return true;
  } catch (error) {
    console.error('   :', error);
    return false;
  }
};

//    
export const getPushNotificationStatus = (): {
  supported: boolean;
  permission: NotificationPermission;
  enabled: boolean;
} => {
  const supported = 'Notification' in window && 'serviceWorker' in navigator;
  const permission = supported ? Notification.permission : 'denied';
  const enabled = supported && permission === 'granted';

  return {
    supported,
    permission,
    enabled
  };
};
