import { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { supabase } from '../../lib/supabase';
import Header from '../../components/Header';
import Footer from '../../components/Footer';
import PushNotificationSettings from '../../components/PushNotificationSettings';
import { linkPhoneToPushSubscription } from '../../lib/phoneBasedPush';
import { subscribeToPush } from '../../lib/pushNotification';

interface OrderData {
  id: string;
  status: string;
  order_type: 'delivery' | 'pickup';
  delivery_address?: string;
  delivery_time?: string;
  pickup_time?: string;
  special_requests?: string;
  depositor_name: string;
  subtotal: number;
  total: number;
  created_at: string;
  stores: {
    name: string;
    phone: string;
    bank_account: string;
    account_holder: string;
  };
  order_items: Array<{
    quantity: number;
    price: number;
    menus: {
      name: string;
    };
  }>;
}

export default function OrderComplete() {
  const navigate = useNavigate();
  const { orderId } = useParams();
  const [orderData, setOrderData] = useState<OrderData | null>(null);
  const [loading, setLoading] = useState(true);
  const [copied, setCopied] = useState(false);
  const [pushLinked, setPushLinked] = useState(false);

  useEffect(() => {
    const loadOrderData = async () => {
      if (!orderId) {
        navigate('/');
        return;
      }

      try {
        setLoading(true);
        const { data, error } = await supabase
          .from('orders')
          .select(`
            *,
            stores (
              name,
              phone,
              bank_account,
              account_holder
            ),
            order_items (
              quantity,
              price,
              menus (
                name
              )
            )
          `)
          .eq('id', orderId)
          .single();

        if (error) {
          console.error('   :', error);
          navigate('/stores');
          return;
        }

        setOrderData(data);
      } catch (error) {
        console.error('   :', error);
        navigate('/stores');
      } finally {
        setLoading(false);
      }
    };

    loadOrderData();
  }, [orderId, navigate]);

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-orange-500 mx-auto mb-4"></div>
          <p className="text-gray-600">   ...</p>
        </div>
      </div>
    );
  }

  if (!orderData) return null;

  const copyAccountNumber = () => {
    if (orderData?.stores?.bank_account) {
      navigator.clipboard.writeText(orderData.stores.bank_account);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    }
  };

  const handleLinkPushNotification = async () => {
    try {
      //   
      const subscription = await subscribeToPush();
      if (!subscription) {
        alert('   .');
        return;
      }

      //    
      if (orderData?.customer_phone) {
        const success = await linkPhoneToPushSubscription(
          orderData.customer_phone, 
          subscription
        );
        
        if (success) {
          setPushLinked(true);
          alert('  !        .');
        } else {
          alert('   .');
        }
      }
    } catch (error) {
      console.error('   :', error);
      alert('     .');
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />
      
      <div className="max-w-md mx-auto p-4 space-y-6">
        {/*     */}
        <div className="bg-white rounded-2xl p-6 shadow-lg text-center">
          <div className="w-20 h-20 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <i className="ri-check-line text-3xl text-green-500"></i>
          </div>
          <h1 className="text-2xl font-bold text-gray-800 mb-2"> !</h1>
          <p className="text-gray-600 mb-4">{orderData.stores.name}</p>
          
          <div className="bg-orange-50 border border-orange-200 rounded-xl p-4 mb-4">
            <p className="text-orange-800 font-medium text-sm">
                  
            </p>
          </div>

          {/*   -      */}
          <div className="text-center text-sm text-gray-600">
            <p>   </p>
            <p className="font-medium text-orange-600">{orderData.stores.phone}</p>
            <p> </p>
          </div>
        </div>

        {/*     */}
        <div className="bg-white rounded-2xl p-6 shadow-lg">
          {/*    */}
          <div className="mb-6">
            <h2 className="text-lg font-bold text-gray-800 mb-4"> </h2>
            
            <div className="bg-gray-50 rounded-xl p-4 mb-4">
              <div className="text-sm text-gray-600 mb-2"> </div>
              <div className="font-mono text-lg font-bold text-gray-800 mb-1">
                {orderData.stores.bank_account}
              </div>
              <div className="text-sm text-gray-600">
                : {orderData.stores.account_holder}
              </div>
            </div>

            <button
              onClick={copyAccountNumber}
              className="bg-orange-500 hover:bg-orange-600 text-white px-6 py-2 rounded-lg font-medium transition-colors"
            >
              <i className="ri-file-copy-line mr-1"></i>
              
            </button>
          </div>

          {/*  */}
          <div className="flex items-center my-6">
            <div className="flex-1 h-px bg-gray-200"></div>
          </div>

          {/* /   */}
          <div className="mb-6">
            <h2 className="text-lg font-bold text-gray-800 mb-4">
              {orderData.order_type === 'delivery' ? ' ' : ' '}
            </h2>
            
            <div className="space-y-3 mb-6">
              {orderData.delivery_address && (
                <div className="flex items-start gap-3">
                  <i className="ri-map-pin-line text-orange-500 mt-1"></i>
                  <div>
                    <div className="text-sm text-gray-600"> </div>
                    <div className="text-gray-800">{orderData.delivery_address}</div>
                  </div>
                </div>
              )}
              
              {orderData.delivery_time && (
                <div className="flex items-start gap-3">
                  <i className="ri-time-line text-orange-500 mt-1"></i>
                  <div>
                    <div className="text-sm text-gray-600"> </div>
                    <div className="text-gray-800">{orderData.delivery_time}</div>
                  </div>
                </div>
              )}
              
              {orderData.pickup_time && (
                <div className="flex items-start gap-3">
                  <i className="ri-time-line text-orange-500 mt-1"></i>
                  <div>
                    <div className="text-sm text-gray-600"> </div>
                    <div className="text-gray-800">{orderData.pickup_time}</div>
                  </div>
                </div>
              )}
              
              {orderData.special_requests && (
                <div className="flex items-start gap-3">
                  <i className="ri-message-line text-orange-500 mt-1"></i>
                  <div>
                    <div className="text-sm text-gray-600"></div>
                    <div className="text-gray-800">{orderData.special_requests}</div>
                  </div>
                </div>
              )}
            </div>
          </div>

          {/*   */}
          <div className="border-t border-gray-200 pt-4">
            <div className="space-y-3 mb-4">
              {orderData.order_items.map((item, index) => (
                <div key={index} className="flex justify-between items-center py-2 border-b border-gray-100 last:border-b-0">
                  <span className="text-gray-700">{item.menus.name} x {item.quantity}</span>
                  <span className="font-medium text-gray-800">{(item.price * item.quantity).toLocaleString()}</span>
                </div>
              ))}
            </div>
            
            <div className="border-t border-gray-200 pt-4">
              <div className="flex justify-between items-center">
                <span className="text-lg font-bold text-gray-800">  </span>
                <span className="text-2xl font-bold text-orange-500">{orderData.total.toLocaleString()}</span>
              </div>
            </div>
          </div>
        </div>

        {/*    */}
        <div className="mb-6">
          {!pushLinked ? (
            <div className="bg-orange-50 border border-orange-200 rounded-lg p-4">
              <div className="flex items-center justify-between">
                <div className="flex items-center">
                  <i className="ri-notification-line text-orange-500 text-xl mr-3"></i>
                  <div>
                    <h3 className="text-sm font-medium text-orange-800">  </h3>
                    <p className="text-xs text-orange-600 mt-1">
                       ,       .
                    </p>
                  </div>
                </div>
                <button
                  onClick={handleLinkPushNotification}
                  className="bg-orange-500 hover:bg-orange-600 text-white px-4 py-2 rounded-lg text-sm font-medium transition-colors"
                >
                   
                </button>
              </div>
            </div>
          ) : (
            <div className="bg-green-50 border border-green-200 rounded-lg p-4">
              <div className="flex items-center">
                <i className="ri-notification-3-line text-green-500 text-xl mr-3"></i>
                <div>
                  <h3 className="text-sm font-medium text-green-800">  </h3>
                  <p className="text-xs text-green-600 mt-1">
                           .
                  </p>
                </div>
              </div>
            </div>
          )}
        </div>

        {/*   */}
        <div className="bg-blue-50 border border-blue-200 rounded-2xl p-6">
          <div className="flex items-start gap-3">
            <i className="ri-message-line text-blue-500 text-xl mt-1"></i>
            <div>
              <h3 className="font-bold text-blue-800 mb-2">  </h3>
              <p className="text-blue-700 text-sm">
                 ,        . 
                  SMS      .
              </p>
            </div>
          </div>
        </div>

      </div>

      {/*    */}
      {copied && (
        <div className="fixed top-20 left-1/2 transform -translate-x-1/2 bg-orange-500 text-white px-6 py-3 rounded-xl text-sm font-medium z-50 shadow-lg flex items-center gap-2">
          <i className="ri-check-line text-base"></i>
           
        </div>
      )}
      
      <Footer />
    </div>
  );
}