//     (  +  )
export const getPendingUsers = async () => {
  try {
    // 1.    
    const { data: users, error: usersError } = await supabase
      .from('users')
      .select('*')
      .eq('role', 'admin')
      .order('created_at', { ascending: false });

    if (usersError) {
      console.error('  :', usersError);
      throw usersError;
    }

    if (!users || users.length === 0) {
      return [];
    }

    // 2.      
    const usersWithStores = await Promise.all(
      users.map(async (user) => {
        try {
          const { data: userStores } = await supabase
            .from('user_stores')
            .select(`
              store_id,
              stores (
                name
              )
            `)
            .eq('user_id', user.id)
            .limit(1);

          return {
            ...user,
            store_name: userStores?.[0]?.stores?.name || null
          };
        } catch (error) {
          console.error(` ${user.id}    :`, error);
          return {
            ...user,
            store_name: null
          };
        }
      })
    );

    return usersWithStores;
  } catch (error) {
    console.error('  :', error);
    throw error;
  }
};

//  
export const approveUser = async (userId: string, storeId: string) => {
  try {
    //    
    const { error: userError } = await supabase
      .from('users')
      .update({ 
        status: 'approved',
        updated_at: new Date().toISOString()
      })
      .eq('id', userId);

    if (userError) {
      throw userError;
    }

    // user_stores  
    const { error: storeError } = await supabase
      .from('user_stores')
      .insert({
        user_id: userId,
        store_id: storeId,
        role: 'owner',
        approved_at: new Date().toISOString()
      });

    if (storeError) {
      throw storeError;
    }

    return { success: true };
  } catch (error) {
    console.error('  :', error);
    throw error;
  }
};

//  
export const rejectUser = async (userId: string) => {
  try {
    const { error } = await supabase
      .from('users')
      .update({ 
        status: 'rejected',
        updated_at: new Date().toISOString()
      })
      .eq('id', userId);

    if (error) {
      throw error;
    }

    return { success: true };
  } catch (error) {
    console.error('  :', error);
    throw error;
  }
};
