import { supabase } from './supabase';

//   
export const getUserProfile = async (userId: string) => {
  const { data, error } = await supabase
    .from('users')
    .select('*')
    .eq('id', userId)
    .single();

  if (error) {
    console.error('   :', error);
    throw error;
  }

  return data;
};

//   
export const createUserProfile = async (userData: {
  id: string;
  email: string;
  name: string;
  profile_image?: string;
  phone?: string;
}) => {
  const { data, error } = await supabase
    .from('users')
    .insert({
      id: userData.id,
      email: userData.email,
      name: userData.name,
      profile_image: userData.profile_image || '',
      phone: userData.phone || '',
      role: 'customer'
    })
    .select()
    .single();

  if (error) {
    console.error('   :', error);
    throw error;
  }

  return data;
};

//    ( )
export const addUserToStore = async (userId: string, storeId: string, role: 'owner' | 'manager' = 'owner') => {
  const { data, error } = await supabase
    .from('user_stores')
    .insert({
      user_id: userId,
      store_id: storeId,
      role
    })
    .select()
    .single();

  if (error) {
    console.error('-  :', error);
    throw error;
  }

  return data;
};

//    
export const getUserStores = async (userId: string) => {
  const { data, error } = await supabase
    .from('user_stores')
    .select(`
      *,
      stores (
        id,
        name,
        category,
        delivery_area,
        phone,
        minimum_order_amount,
        bank_account,
        account_holder
      )
    `)
    .eq('user_id', userId)
    .order('created_at', { ascending: false });

  if (error) {
    console.error('    :', error);
    throw error;
  }

  return data || [];
};
