import { supabase } from './supabase';

//    
export const savePushSubscription = async (userId: string, subscription: any): Promise<boolean> => {
  try {
    const { error } = await supabase
      .from('user_push_subscriptions')
      .upsert({
        user_id: userId,
        subscription: subscription,
        created_at: new Date().toISOString(),
        updated_at: new Date().toISOString()
      });

    if (error) {
      console.error('    :', error);
      return false;
    }

    return true;
  } catch (error) {
    console.error('    :', error);
    return false;
  }
};

//     
export const getPushSubscription = async (userId: string): Promise<any> => {
  try {
    const { data, error } = await supabase
      .from('user_push_subscriptions')
      .select('subscription')
      .eq('user_id', userId)
      .single();

    if (error) {
      console.error('    :', error);
      return null;
    }

    return data?.subscription;
  } catch (error) {
    console.error('    :', error);
    return null;
  }
};

//    ( )
export const sendPushNotification = async (
  userId: string, 
  title: string, 
  body: string, 
  data?: any
): Promise<boolean> => {
  try {
    const subscription = await getPushSubscription(userId);
    if (!subscription) {
      console.log('    .');
      return false;
    }

    //      
    //       
    if ('serviceWorker' in navigator && 'PushManager' in window) {
      const registration = await navigator.serviceWorker.ready;
      await registration.showNotification(title, {
        body,
        icon: '/favicon.ico',
        badge: '/favicon.ico',
        data,
        vibrate: [200, 100, 200],
        actions: [
          {
            action: 'view',
            title: ''
          },
          {
            action: 'close',
            title: ''
          }
        ]
      });
    }

    return true;
  } catch (error) {
    console.error('   :', error);
    return false;
  }
};

//     
export const getOrderNotificationMessage = (status: string, orderId: string): { title: string; body: string } => {
  switch (status) {
    case '':
      return {
        title: '  ',
        body: ` ${orderId}  .   !`
      };
    case '':
      return {
        title: ' ',
        body: ` ${orderId}  .  !`
      };
    case '':
      return {
        title: ' ',
        body: ` ${orderId}   .`
      };
    case '':
      return {
        title: ' ',
        body: ` ${orderId} .`
      };
    default:
      return {
        title: '  ',
        body: ` ${orderId}  .`
      };
  }
};

//     
export const getStoreOrderNotificationMessage = (storeName: string, orderId: string): { title: string; body: string } => {
  return {
    title: '  ',
    body: `${storeName}   ! (: ${orderId})`
  };
};
