//    
export const sendTestPushNotification = async (title: string = ' ', body: string = '   !') => {
  try {
    if (!('serviceWorker' in navigator)) {
      alert('  Service Worker  .');
      return false;
    }

    if (!('PushManager' in window)) {
      alert('     .');
      return false;
    }

    // 1.   
    if (Notification.permission === 'default') {
      const permission = await Notification.requestPermission();
      if (permission !== 'granted') {
        alert('  .    .');
        return false;
      }
    } else if (Notification.permission === 'denied') {
      alert('  .    .');
      return false;
    }

    const registration = await navigator.serviceWorker.ready;
    
    await registration.showNotification(title, {
      body,
      icon: '/favicon.ico',
      badge: '/favicon.ico',
      vibrate: [200, 100, 200],
      data: {
        test: true,
        timestamp: Date.now()
      },
      actions: [
        {
          action: 'test',
          title: ' '
        }
      ]
    });

    console.log('    !');
    return true;
  } catch (error) {
    console.error('    :', error);
    alert('   : ' + error.message);
    return false;
  }
};

//    
export const diagnosePushNotification = async () => {
  const results = {
    browserSupport: false,
    serviceWorkerSupport: false,
    pushSupport: false,
    permission: 'default',
    serviceWorkerRegistered: false,
    pushSubscription: null
  };

  try {
    // 1.   
    results.browserSupport = 'Notification' in window;
    console.log('  :', results.browserSupport);

    // 2. Service Worker  
    results.serviceWorkerSupport = 'serviceWorker' in navigator;
    console.log(' Service Worker :', results.serviceWorkerSupport);

    // 3.   
    results.pushSupport = 'PushManager' in window;
    console.log('  :', results.pushSupport);

    // 4.   
    results.permission = Notification.permission;
    console.log('  :', results.permission);

    // 5. Service Worker  
    if (results.serviceWorkerSupport) {
      const registrations = await navigator.serviceWorker.getRegistrations();
      results.serviceWorkerRegistered = registrations.length > 0;
      console.log(' Service Worker :', results.serviceWorkerRegistered);
    }

    // 6.   
    if (results.serviceWorkerRegistered) {
      const registration = await navigator.serviceWorker.ready;
      const subscription = await registration.pushManager.getSubscription();
      results.pushSubscription = subscription;
      console.log('  :', subscription ? '' : '');
    }

    return results;
  } catch (error) {
    console.error('   :', error);
    return results;
  }
};

