import { usePushNotification } from '../hooks/usePushNotification';
import { useNewAuth } from '../hooks/useNewAuth';

export default function PushNotificationSettings() {
  const { user } = useNewAuth();
  const { 
    isSupported, 
    permission, 
    isEnabled, 
    isLoading, 
    requestPermission 
  } = usePushNotification(user?.id || null);

  const handleEnablePush = async () => {
    const success = await requestPermission();
    if (success) {
      alert('  !');
    } else {
      alert('   .   .');
    }
  };

  if (!isSupported) {
    return (
      <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
        <div className="flex items-center">
          <i className="ri-error-warning-line text-yellow-500 text-xl mr-3"></i>
          <div>
            <h3 className="text-sm font-medium text-yellow-800">    </h3>
            <p className="text-xs text-yellow-600 mt-1">
              Chrome, Firefox, Safari    .
            </p>
          </div>
        </div>
      </div>
    );
  }

  if (permission === 'denied') {
    return (
      <div className="bg-red-50 border border-red-200 rounded-lg p-4">
        <div className="flex items-center">
          <i className="ri-notification-off-line text-red-500 text-xl mr-3"></i>
          <div>
            <h3 className="text-sm font-medium text-red-800">  </h3>
            <p className="text-xs text-red-600 mt-1">
                 .
            </p>
          </div>
        </div>
      </div>
    );
  }

  if (isEnabled) {
    return (
      <div className="bg-green-50 border border-green-200 rounded-lg p-4">
        <div className="flex items-center">
          <i className="ri-notification-3-line text-green-500 text-xl mr-3"></i>
          <div>
            <h3 className="text-sm font-medium text-green-800">  </h3>
            <p className="text-xs text-green-600 mt-1">
                      .
            </p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
      <div className="flex items-center justify-between">
        <div className="flex items-center">
          <i className="ri-notification-line text-blue-500 text-xl mr-3"></i>
          <div>
            <h3 className="text-sm font-medium text-blue-800">  </h3>
            <p className="text-xs text-blue-600 mt-1">
              SMS        .
            </p>
          </div>
        </div>
        <button
          onClick={handleEnablePush}
          disabled={isLoading}
          className="bg-blue-500 hover:bg-blue-600 disabled:bg-blue-300 text-white px-4 py-2 rounded-lg text-sm font-medium transition-colors flex items-center"
        >
          {isLoading ? (
            <>
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
               ...
            </>
          ) : (
            <>
              <i className="ri-notification-line mr-2"></i>
               
            </>
          )}
        </button>
      </div>
    </div>
  );
}
