import { useState, useEffect } from 'react';
import { supabase } from '../lib/supabase';
import { getUserProfile, createUserProfile } from '../lib/userApi';
import type { User } from '@supabase/supabase-js';

interface UserProfile {
  id: string;
  email: string;
  name: string;
  profile_image: string;
  phone: string;
  role: 'customer' | 'owner' | 'admin' | 'super_admin';
  created_at: string;
  updated_at: string;
}

export const useAuth = () => {
  const [user, setUser] = useState<User | null>(null);
  const [userProfile, setUserProfile] = useState<UserProfile | null>(null);
  const [loading, setLoading] = useState(true);
  const [initialLoadComplete, setInitialLoadComplete] = useState(false);
  const [isInitialized, setIsInitialized] = useState(false);

  useEffect(() => {
    //     
    if (isInitialized) return;
    
    const fetchUserProfile = async (userId: string) => {
      try {
        return await getUserProfile(userId);
      } catch (error) {
        console.log('  .  .');
        return null;
      }
    };

    const createUserProfileData = async (user: User) => {
      try {
        return await createUserProfile({
          id: user.id,
          email: user.email || '',
          name: user.user_metadata?.full_name || user.user_metadata?.name || '',
          profile_image: user.user_metadata?.avatar_url || user.user_metadata?.picture || '',
          phone: user.user_metadata?.phone_number || '',
        });
      } catch (error) {
        console.error('   :', error);
        //     
        return {
          id: user.id,
          email: user.email || '',
          name: user.user_metadata?.full_name || user.user_metadata?.name || '',
          profile_image: user.user_metadata?.avatar_url || user.user_metadata?.picture || '',
          phone: user.user_metadata?.phone_number || '',
          role: 'customer' as const,
          created_at: new Date().toISOString(),
          updated_at: new Date().toISOString(),
        };
      }
    };

    const getInitialSession = async () => {
      try {
        console.log('    ...');
        const { data: { session }, error } = await supabase.auth.getSession();
        
        if (error) {
          console.error('   :', error);
          setUser(null);
          setUserProfile(null);
        } else if (session?.user) {
          console.log('  :', session.user.email);
          setUser(session.user);
          
          //   / ( )
          try {
            const profilePromise = fetchUserProfile(session.user.id);
            const timeoutPromise = new Promise((_, reject) => 
              setTimeout(() => reject(new Error('  ')), 5000)
            );
            
            let profile = await Promise.race([profilePromise, timeoutPromise]) as any;
            
            if (!profile) {
              console.log('    .');
              profile = await createUserProfileData(session.user);
            } else {
              console.log('  :', profile.role);
            }
            setUserProfile(profile);
          } catch (profileError) {
            console.error('   :', profileError);
            //       
            const defaultProfile = await createUserProfileData(session.user);
            setUserProfile(defaultProfile);
          }
        } else {
          console.log('  .');
          setUser(null);
          setUserProfile(null);
        }
      } catch (error) {
        console.error('    :', error);
        setUser(null);
        setUserProfile(null);
      } finally {
        setLoading(false);
        setInitialLoadComplete(true);
        setIsInitialized(true);
      }
    };

    getInitialSession();

    const { data: { subscription } } = supabase.auth.onAuthStateChange(
      async (event, session) => {
        console.log('   :', event, session?.user?.email);
        
        //       
        if (!initialLoadComplete) {
          return;
        }
        
        if (session?.user) {
          console.log('  :', session.user.email);
          setUser(session.user);
          
          //   /
          let profile = await fetchUserProfile(session.user.id);
          if (!profile) {
            console.log('    .');
            profile = await createUserProfileData(session.user);
          } else {
            console.log('  :', profile.role);
          }
          setUserProfile(profile);
        } else {
          console.log('  ');
          setUser(null);
          setUserProfile(null);
        }
      }
    );

    return () => {
      subscription.unsubscribe();
    };
  }, [isInitialized]);

  return { user, userProfile, loading };
};