export const getOrderDetails = async (orderId: string) => {
  const { data, error } = await supabase
    .from('orders')
    .select(`
      *,
      stores (
        id,
        name,
        phone,
        bank_account,
        account_holder
      ),
      users (
        id,
        name,
        phone
      ),
      order_items (
        id,
        quantity,
        price,
        menus (
          id,
          name,
          category
        )
      )
    `)
    .eq('id', orderId)
    .single();

  if (error) {
    console.error('    :', error);
    throw error;
  }

  return data;
};
