import { supabase } from './supabase';
import { StoreDB, CreateStoreData, UpdateStoreData } from '../types';
import { cache, CACHE_KEYS } from './cache';

//    
export const getStores = async (): Promise<StoreDB[]> => {
  console.log(' getStores API  ');
  
  //   
  const cachedData = cache.get<StoreDB[]>(CACHE_KEYS.STORES);
  if (cachedData) {
    console.log(' getStores  :', cachedData.length, '');
    return cachedData;
  }
  
  const { data, error } = await supabase
    .from('stores')
    .select('*')
    .order('created_at', { ascending: false });

  if (error) {
    console.error('    :', error);
    throw new Error(`   : ${error.message}`);
  }

  const result = data || [];
  console.log(' getStores API :', result);
  
  //   (5 TTL)
  cache.set(CACHE_KEYS.STORES, result, 5 * 60 * 1000);
  
  return result;
};

//    
export const getStore = async (storeId: string): Promise<StoreDB> => {
  if (!storeId) {
    throw new Error(' ID .');
  }

  const { data, error } = await supabase
    .from('stores')
    .select('*')
    .eq('id', storeId)
    .single();

  if (error) {
    console.error('   :', error);
    throw new Error(`   : ${error.message}`);
  }

  return data;
};

//  
export const createStore = async (storeData: CreateStoreData): Promise<StoreDB> => {
  //   
  if (!storeData.name?.trim()) {
    throw new Error(' .');
  }
  if (!storeData.category?.trim()) {
    throw new Error(' .');
  }
  if (!storeData.delivery_area?.trim()) {
    throw new Error(' .');
  }
  if (!storeData.phone?.trim()) {
    throw new Error(' .');
  }
  if (!storeData.bank_account?.trim()) {
    throw new Error(' .');
  }
  if (!storeData.account_holder?.trim()) {
    throw new Error(' .');
  }

  const { data, error } = await supabase
    .from('stores')
    .insert(storeData)
    .select()
    .single();

  if (error) {
    console.error('  :', error);
    throw new Error(`  : ${error.message}`);
  }

  //  
  cache.delete(CACHE_KEYS.STORES);
  
  return data;
};

//   
export const updateStore = async (storeId: string, updateData: UpdateStoreData): Promise<StoreDB> => {
  console.log(' updateStore API :', storeId, updateData);
  console.log('  API :', updateData.minimum_order_amount);
  
  if (!storeId) {
    throw new Error(' ID .');
  }
  if (updateData.name !== undefined && !updateData.name?.trim()) {
    throw new Error(' .');
  }
  if (updateData.category !== undefined && !updateData.category?.trim()) {
    throw new Error(' .');
  }

  const { data, error } = await supabase
    .from('stores')
    .update(updateData)
    .eq('id', storeId)
    .select()
    .single();

  if (error) {
    console.error('  :', error);
    throw new Error(`  : ${error.message}`);
  }

  console.log(' updateStore API :', data);
  console.log('  API :', data?.minimum_order_amount);

  //  
  cache.delete(CACHE_KEYS.STORES);
  
  return data;
};

//  
export const deleteStore = async (storeId: string): Promise<void> => {
  if (!storeId) {
    throw new Error(' ID .');
  }

  const { error } = await supabase
    .from('stores')
    .delete()
    .eq('id', storeId);

  if (error) {
    console.error('  :', error);
    throw new Error(`  : ${error.message}`);
  }

  //  
  cache.delete(CACHE_KEYS.STORES);
};

//     
export const getUserStores = async (userId: string): Promise<StoreDB[]> => {
  console.log(' getUserStores API  , userId:', userId);
  
  if (!userId) {
    throw new Error(' ID .');
  }

  //   
  const cacheKey = `${CACHE_KEYS.USER_STORES}_${userId}`;
  
  //   
  const cachedData = cache.get<StoreDB[]>(cacheKey);
  if (cachedData) {
    console.log(' getUserStores  :', cachedData.length, '');
    return cachedData;
  }

  const { data, error } = await supabase
    .from('user_stores')
    .select(`
      stores (
        id,
        name,
        category,
        delivery_area,
        phone,
        business_hours_start,
        business_hours_end,
        order_cutoff_time,
        minimum_order_amount,
        pickup_time_slots,
        delivery_time_slots,
        bank_account,
        account_holder,
        created_at,
        updated_at
      )
    `)
    .eq('user_id', userId)
    .order('created_at', { ascending: false });

  if (error) {
    console.error('     :', error);
    throw new Error(`   : ${error.message}`);
  }

  //   stores  
  const result = data?.map(item => item.stores).filter(Boolean) || [];
  console.log(' getUserStores API :', result);
  
  //   (5 TTL)
  cache.set(cacheKey, result, 5 * 60 * 1000);
  
  return result;
};

//     
export const addStoreToUser = async (userId: string, storeId: string): Promise<void> => {
  console.log(' addStoreToUser API , userId:', userId, 'storeId:', storeId);
  
  if (!userId) {
    throw new Error(' ID .');
  }
  if (!storeId) {
    throw new Error(' ID .');
  }

  const { error } = await supabase
    .from('user_stores')
    .insert({
      user_id: userId,
      store_id: storeId
    });

  if (error) {
    console.error('    :', error);
    throw new Error(`  : ${error.message}`);
  }

  //  
  const cacheKey = `${CACHE_KEYS.USER_STORES}_${userId}`;
  cache.delete(cacheKey);
  
  console.log('    ');
};

